@import url(https://fonts.googleapis.com/css2?family=Allison&family=Josefin+Sans:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100;1,200;1,300;1,400;1,500;1,600;1,700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Spartan:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

/* font-family: 'Allison', cursive;
font-family: 'Josefin Sans', sans-serif;
font-family: 'Poppins', sans-serif;
font-family: 'Spartan', sans-serif; */

body {
  overflow-x: hidden;
  background-image: linear-gradient(
      0deg,
      rgba(0, 0, 0, 0.25),
      rgba(0, 0, 0, 0.25)
    ),
    url(/PrathimaPramod/static/media/sunset.89decb8b.jpg);
  background-color: aliceblue;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}

* {
  font-family: "Poppins", sand-serif;
}

#Header {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  color: aliceblue;
  text-shadow: 0 0 4px rgb(70, 40, 0), 0 0 3px rgb(70, 40, 0),
    0 0 2px rgb(70, 40, 0);
}

#Header .headNav,
#Header .burger {
  display: none;
  z-index: 1;
}

#Header a {
  padding-left: 03%;
  padding-right: 03%;
  text-decoration: none;
  color: inherit;
  font-weight: 500;
  font-size: 20px;
  font-family: "Josefin Sans", sans-serif;
  transition: color 0.5s;
}

#Header a:hover {
  color: rgb(0, 174, 255);
}

#Header .headerMid {
  font-family: "Allison", cursive;
  font-size: 60px;
  font-weight: 400;
}

#Header .nav-links {
  display: flex;
  position: absolute;
  right: 0%;
  top: 0%;
  padding-top: 60px;
  padding-bottom: 110px;
  height: 100vh;
  width: 75%;
  background-color: rgba(0, 140, 255, 0.9);
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  transform: translateX(100%);
  transition: transform 0.5s ease-in-out;
}
#Header .nav-active {
  transform: translateX(0%);
}
#Header .nav-links li {
  list-style: none;
  opacity: 0;
}
#Header .burger div {
  width: 25px;
  height: 3px;
  background-color: aliceblue;
  margin: 5px;
  transition: all 0.3s ease;
  box-shadow: 0 0 4px rgb(70, 40, 0), 0 0 3px rgb(70, 40, 0),
    0 0 2px rgb(70, 40, 0);
}

#Header .toggle .line1 {
  transform: rotate(-45deg) translate(-5px, 6px);
}
#Header .toggle .line2 {
  opacity: 0;
}
#Header .toggle .line3 {
  transform: rotate(45deg) translate(-5px, -6px);
}

@media only screen and (max-width: 768px) {
  #Header .headNav,
  #Header .burger {
    display: block;
  }
  #Header {
    justify-content: space-around;
  }
  #Header > a {
    display: none;
  }
  #Header .headerMid {
    display: block;
    font-size: 45px;
  }
}

@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

#Home {
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 10%;
  color: aliceblue;
  text-shadow: 0 0 4px rgb(70, 40, 0), 0 0 3px rgb(70, 40, 0),
    0 0 2px rgb(70, 40, 0);
}

#Home * {
  margin-top: 1.5%;
  margin-bottom: 1.5%;
}

#Home h1 {
  width: 51%;
}

#Home p {
  font-size: large;
}

#Home button {
  padding: 10px;
  background-color: grey;
  border-radius: 15px;
  border-width: 0;
  transition: background-color 0.5s;
  font-size: medium;
  box-shadow: 0 0 5px lightgray, 0 0 2px lightgray;
}

#Home button:hover {
  background-color: orange;
}

@media only screen and (max-width: 768px) {
  #Home * {
    margin-top: 7%;
    margin-bottom: 7%;
  }
  #Home h1 {
    width: 70%;
  }
}

#About {
  display: flex;
  flex-direction: column;
  padding-left: 7%;
  padding-right: 7%;
}
#About .aboutMain {
  display: flex;
  align-items: center;
  height: 100vh;
}

#About .aboutMain .textCont {
  margin-left: 05%;
  padding: 03%;
  padding-right: 01%;
  background-color: rgba(240, 248, 255, 0.9);
  width: 60%;
}

#About .aboutMain .textCont p {
  font-size: large;
}

#About .aboutMain .imgCont {
  z-index: -1;
  height: 70%;
  width: 30%;
  position: absolute;
  left: 55%;
  border-radius: 20px;
  overflow: hidden;
}

#About .aboutMain .imgCont img {
  opacity: 0.9;
  height: 100%;
  width: 100%;
  object-fit: cover;
}

#About .aboutMe {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#About .aboutMe div {
  width: 70%;
  padding: 05%;
  background-color: rgba(240, 248, 255, 0.9);
}

#About .aboutMe p {
  font-size: large;
}

#About .aboutEdu {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50vh;
}

#About .aboutEdu div {
  width: 40%;
  background-color: rgba(240, 248, 255, 0.8);
  padding: 2%;
  margin: 1%;
}
#About .eduHead {
  background-color: rgba(240, 248, 255, 0.58) !important;
}
#About .aboutEdu li {
  font-size: large;
}
#About .aboutEdu img {
  position: absolute;
  z-index: -1;
  height: 50%;
  left: 33%;
  width: auto;
  object-fit: contain;
  border-radius: 50%;
}

@media only screen and (max-width: 768px) {
  #About {
    padding-left: 2%;
    padding-right: 2%;
  }
  #About .aboutMain .imgCont {
    left: 40%;
    height: 70%;
    width: 55%;
  }
  #About .aboutMe {
    height: 70vh;
  }
  #About .aboutMe div {
    width: 90%;
    padding: 03%;
  }
  #About .aboutMe p {
    font-size: small;
  }
  #About .aboutEdu h1 {
    font-size: medium;
  }
  #About .aboutEdu div {
    width: 60%;
  }
  #About .eduHead {
    width: 45% !important;
  }
  #About .aboutEdu li {
    font-size: small;
  }
  #About .aboutEdu img {
    height: 28%;
    left: 13.5%;
  }
}

